import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  value: "translateX(-100%)",
}

export const Drawer = createSlice({
  name: 'counter',
  initialState,
  reducers: {
    open: (state) => {
    
      state.value = "none"
    },
    close: (state) => {
      state.value = false
    },
 
  },
})

// Action creators are generated for each case reducer function
export const { open, close } = Drawer.actions

export default Drawer.reducer