import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import axios from "axios";
import Header from "../Component/Header";
import Sidebar from "../Component/Sidebar";
import { ToastContainer, toast } from "react-toastify";
var jwt = require("jsonwebtoken");
export default function CustomerLoanApply() {
  const [listcustomer, setlistcustomer] = useState([]);
  const [listorg, setlistOrg] = useState([]);
  const [orgID, setorgID] = useState();
  const [interest, setinterest] = useState([]);
  const [interestbranch, setinterestbranch] = useState([]);
  const [showotp, setshowotp] = useState(false);
  const [alertmessagehandler, setalertmessagehandler] = useState(false);
  const [otp, setotp] = useState();
  let token = localStorage.getItem("token");
  var decode1 = jwt.decode(token);
  let custID = decode1.custID;
  console.log("token==", token);

  const customerlist = () => {
    axios.post(`/custdetail/${custID}`).then((resp) => {
      let data = resp.data.obj;
      setlistcustomer(data);
      // console.log( "customer list222222 ",data);
    });
  };

  const getOrgForLoan = () => {
    axios.post(`/getOrgForLoan/${custID}`).then((resp) => {
      let data1 = resp.data.final;
      setlistOrg(data1);
      console.log(data1, resp);
    });
  };

  const loantype = (orgID) => {
    // setalertmessagehandler(false);

    axios.post(`/getOrgLoans/${orgID}`).then((resp) => {
      let data = resp.data.values;
      setinterest(data);
      setalertmessagehandler(false);
    });
  };

  const loanintrest = (formdata) => {
    axios
      .post(`/getInterestOFLoan/${orgID}`, { LoanType: formdata })
      .then((resp) => {
        let data = resp.data.item;
        setinterestbranch(Object.values(data));
        setalertmessagehandler(false);
      });
  };

  const AddFormotp = (e) => {
    e.preventDefault();

    axios.post(`/send_Loan_Otp/${custID}`).then((res) => {
      if (res.status) {
        let data = res.data;
        if (data.status) {
          toast.success(data.msg);
        } else {
          toast.success(data.msg);
        }
      }
    });
  };

  const loanformhandler = (e) => {
    setshowotp(false);
    e.preventDefault();

    const data = new FormData(e.target);
    const Formvlaues = Object.fromEntries(data.entries());
    const formData = Formvlaues;
    // console.log('++++++',formData);
    axios
      .post(`/calculate_Amount_cust/${token}`, formData)
      .then((res) => {
        console.log(" @@@@@@@@@@@@@@@@@@@@@@@", res);
        if (res.status) {
          let data = res.data;
          if (data.status) {
            toast.success(data.msg);
          } else {
            toast.error(data.msg);
          }
        } else {
          toast.error(data.msg);
        }
      })
      .catch(console.error());
  };

  useEffect(() => {
    customerlist();
    getOrgForLoan();
  }, []);

  const handleChange = (e) => {
    loantype(e.target.value);
    setorgID(e.target.value);
  };
  const handleinterest = (e) => {
    loanintrest(e.target.value);
  };
  function handleShow() {
    setshowotp(true);
  }
  const alertmessage = () => {
    setalertmessagehandler(true);
  };
  return (
    <>
      <Header />
      <Sidebar />
      <ToastContainer />
      <div
        className="wrapper d-flex flex-column flex-row-fluid mb-0 back-color"
        id="kt_wrapper"
      >
        <div
          className="content d-flex flex-column flex-column-fluid mb-0 py-0"
          id="kt_content"
        >
          <div className="container-xxl mb-0" id="kt_content_container">
            <div className="card-body  pt-9 pb-0 ">
              <div className="col-lg-12">
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-lg-8">
                      <div className="card rounded-15 mb-5 mb-xl-10 bgi-no-repeat bgi-position-x-end bgi-size-cover">
                        <div className="card-body  pt-9 pb-0 ">
                          <div className="d-flex flex-wrap flex-sm-nowrap mb-3">
                            <div className="me-7 mb-4">
                              <div className="symbol symbol-100px symbol-lg-160px symbol-fixed position-relative">
                                <img src={listcustomer.IDphoto} alt="image" />
                                <div className="position-absolute translate-middle bottom-0 start-100 mb-6 bg-success rounded-circle border border-4 border-body h-20px w-20px"></div>
                              </div>
                            </div>

                            <div className="flex-grow-1">
                              <div className="d-flex justify-content-between align-items-start flex-wrap mb-2">
                                <div className="d-flex flex-column">
                                  <div className="d-flex align-items-center mb-2">
                                    <a
                                      href="#"
                                      className="text-gray-900 text-hover-primary fs-2 fw-bold me-1"
                                    >
                                      {listcustomer.fullname}
                                    </a>
                                    <a href="#">
                                      <span className="svg-icon svg-icon-1 svg-icon-primary">
                                        <svg
                                          xmlns="http://www.w3.org/2000/svg"
                                          width="24px"
                                          height="24px"
                                          viewBox="0 0 24 24"
                                        >
                                          <path
                                            d="M10.0813 3.7242C10.8849 2.16438 13.1151 2.16438 13.9187 3.7242V3.7242C14.4016 4.66147 15.4909 5.1127 16.4951 4.79139V4.79139C18.1663 4.25668 19.7433 5.83365 19.2086 7.50485V7.50485C18.8873 8.50905 19.3385 9.59842 20.2758 10.0813V10.0813C21.8356 10.8849 21.8356 13.1151 20.2758 13.9187V13.9187C19.3385 14.4016 18.8873 15.491 19.2086 16.4951V16.4951C19.7433 18.1663 18.1663 19.7433 16.4951 19.2086V19.2086C15.491 18.8873 14.4016 19.3385 13.9187 20.2758V20.2758C13.1151 21.8356 10.8849 21.8356 10.0813 20.2758V20.2758C9.59842 19.3385 8.50905 18.8873 7.50485 19.2086V19.2086C5.83365 19.7433 4.25668 18.1663 4.79139 16.4951V16.4951C5.1127 15.491 4.66147 14.4016 3.7242 13.9187V13.9187C2.16438 13.1151 2.16438 10.8849 3.7242 10.0813V10.0813C4.66147 9.59842 5.1127 8.50905 4.79139 7.50485V7.50485C4.25668 5.83365 5.83365 4.25668 7.50485 4.79139V4.79139C8.50905 5.1127 9.59842 4.66147 10.0813 3.7242V3.7242Z"
                                            fill="currentColor"
                                          />
                                          <path
                                            d="M14.8563 9.1903C15.0606 8.94984 15.3771 8.9385 15.6175 9.14289C15.858 9.34728 15.8229 9.66433 15.6185 9.9048L11.863 14.6558C11.6554 14.9001 11.2876 14.9258 11.048 14.7128L8.47656 12.4271C8.24068 12.2174 8.21944 11.8563 8.42911 11.6204C8.63877 11.3845 8.99996 11.3633 9.23583 11.5729L11.3706 13.4705L14.8563 9.1903Z"
                                            fill="white"
                                          />
                                        </svg>
                                      </span>
                                    </a>
                                  </div>

                                  <div className="d-flex flex-wrap fw-semibold fs-6 mb-4 pe-2">
                                    <a
                                      href="#"
                                      className="d-flex align-items-center text-gray-400 text-hover-primary me-5 mb-2"
                                    >
                                      <span className="svg-icon svg-icon-4 me-1">
                                        <svg
                                          width="18"
                                          height="18"
                                          viewBox="0 0 18 18"
                                          fill="none"
                                          xmlns="http://www.w3.org/2000/svg"
                                        >
                                          <path
                                            opacity="0.3"
                                            d="M16.5 9C16.5 13.125 13.125 16.5 9 16.5C4.875 16.5 1.5 13.125 1.5 9C1.5 4.875 4.875 1.5 9 1.5C13.125 1.5 16.5 4.875 16.5 9Z"
                                            fill="currentColor"
                                          />
                                          <path
                                            d="M9 16.5C10.95 16.5 12.75 15.75 14.025 14.55C13.425 12.675 11.4 11.25 9 11.25C6.6 11.25 4.57499 12.675 3.97499 14.55C5.24999 15.75 7.05 16.5 9 16.5Z"
                                            fill="currentColor"
                                          />
                                          <rect
                                            x="7"
                                            y="6"
                                            width="4"
                                            height="4"
                                            rx="2"
                                            fill="currentColor"
                                          />
                                        </svg>
                                      </span>
                                      Farmer
                                    </a>
                                    <a
                                      href="#"
                                      className="d-flex align-items-center text-gray-400 text-hover-primary me-5 mb-2"
                                    >
                                      <span className="svg-icon svg-icon-4 me-1">
                                        <svg
                                          width="24"
                                          height="24"
                                          viewBox="0 0 24 24"
                                          fill="none"
                                          xmlns="http://www.w3.org/2000/svg"
                                        >
                                          <path
                                            opacity="0.3"
                                            d="M18.0624 15.3453L13.1624 20.7453C12.5624 21.4453 11.5624 21.4453 10.9624 20.7453L6.06242 15.3453C4.56242 13.6453 3.76242 11.4453 4.06242 8.94534C4.56242 5.34534 7.46242 2.44534 11.0624 2.04534C15.8624 1.54534 19.9624 5.24534 19.9624 9.94534C20.0624 12.0453 19.2624 13.9453 18.0624 15.3453Z"
                                            fill="currentColor"
                                          />
                                          <path
                                            d="M12.0624 13.0453C13.7193 13.0453 15.0624 11.7022 15.0624 10.0453C15.0624 8.38849 13.7193 7.04535 12.0624 7.04535C10.4056 7.04535 9.06241 8.38849 9.06241 10.0453C9.06241 11.7022 10.4056 13.0453 12.0624 13.0453Z"
                                            fill="currentColor"
                                          />
                                        </svg>
                                      </span>
                                      {listcustomer.address}
                                    </a>
                                    <a
                                      href="#"
                                      className="d-flex align-items-center text-gray-400 text-hover-primary mb-2"
                                    >
                                      <span className="svg-icon svg-icon-4 me-1">
                                        <svg
                                          width="24"
                                          height="24"
                                          viewBox="0 0 24 24"
                                          fill="none"
                                          xmlns="http://www.w3.org/2000/svg"
                                        >
                                          <path
                                            opacity="0.3"
                                            d="M21 19H3C2.4 19 2 18.6 2 18V6C2 5.4 2.4 5 3 5H21C21.6 5 22 5.4 22 6V18C22 18.6 21.6 19 21 19Z"
                                            fill="currentColor"
                                          />
                                          <path
                                            d="M21 5H2.99999C2.69999 5 2.49999 5.10005 2.29999 5.30005L11.2 13.3C11.7 13.7 12.4 13.7 12.8 13.3L21.7 5.30005C21.5 5.10005 21.3 5 21 5Z"
                                            fill="currentColor"
                                          />
                                        </svg>
                                      </span>
                                      {listcustomer.email}
                                    </a>
                                  </div>
                                </div>
                              </div>

                              <div className="d-flex flex-wrap flex-stack">
                                <div className="d-flex flex-column flex-grow-1 pe-8">
                                  <div className="d-flex flex-wrap">
                                    <div className="border border-gray-300 border-dashed rounded min-w-125px py-3 px-4 me-6 mb-3">
                                      <div className="d-flex align-items-center">
                                        <span className="svg-icon svg-icon-3 svg-icon-success me-2">
                                          <svg
                                            width="24"
                                            height="24"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            xmlns="http://www.w3.org/2000/svg"
                                          >
                                            <rect
                                              opacity="0.5"
                                              x="13"
                                              y="6"
                                              width="13"
                                              height="2"
                                              rx="1"
                                              transform="rotate(90 13 6)"
                                              fill="currentColor"
                                            />
                                            <path
                                              d="M12.5657 8.56569L16.75 12.75C17.1642 13.1642 17.8358 13.1642 18.25 12.75C18.6642 12.3358 18.6642 11.6642 18.25 11.25L12.7071 5.70711C12.3166 5.31658 11.6834 5.31658 11.2929 5.70711L5.75 11.25C5.33579 11.6642 5.33579 12.3358 5.75 12.75C6.16421 13.1642 6.83579 13.1642 7.25 12.75L11.4343 8.56569C11.7467 8.25327 12.2533 8.25327 12.5657 8.56569Z"
                                              fill="currentColor"
                                            />
                                          </svg>
                                        </span>

                                        <div
                                          className="fs-2 fw-bold"
                                          data-kt-countup="true"
                                          data-kt-countup-value={
                                            listcustomer.totalAmount
                                          }
                                          data-kt-countup-prefix="$"
                                        >
                                          {listcustomer.totalAmount}
                                        </div>
                                      </div>

                                      <div className="fw-semibold fs-6 text-gray-400">
                                        Transaction Amount
                                      </div>
                                    </div>

                                    <div className="border border-gray-300 border-dashed rounded min-w-125px py-3 px-4 me-6 mb-3">
                                      <div className="d-flex align-items-center">
                                        <span className="svg-icon svg-icon-3 svg-icon-danger me-2">
                                          <svg
                                            width="24"
                                            height="24"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            xmlns="http://www.w3.org/2000/svg"
                                          >
                                            <rect
                                              opacity="0.5"
                                              x="11"
                                              y="18"
                                              width="13"
                                              height="2"
                                              rx="1"
                                              transform="rotate(-90 11 18)"
                                              fill="currentColor"
                                            />
                                            <path
                                              d="M11.4343 15.4343L7.25 11.25C6.83579 10.8358 6.16421 10.8358 5.75 11.25C5.33579 11.6642 5.33579 12.3358 5.75 12.75L11.2929 18.2929C11.6834 18.6834 12.3166 18.6834 12.7071 18.2929L18.25 12.75C18.6642 12.3358 18.6642 11.6642 18.25 11.25C17.8358 10.8358 17.1642 10.8358 16.75 11.25L12.5657 15.4343C12.2533 15.7467 11.7467 15.7467 11.4343 15.4343Z"
                                              fill="currentColor"
                                            />
                                          </svg>
                                        </span>

                                        <div
                                          className="fs-2 fw-bold"
                                          data-kt-countup="true"
                                          data-kt-countup-value={
                                            listcustomer.totalTransection
                                          }
                                        >
                                          {listcustomer.totalTransection}
                                        </div>
                                      </div>

                                      <div className="fw-semibold fs-6 text-gray-400">
                                        Total Transaction{" "}
                                      </div>
                                    </div>
                                  </div>
                                  <div className="d-flex align-items-center w-200px w-sm-300px flex-column mt-3">
                                    <div className="d-flex justify-content-between w-100 mt-auto mb-2">
                                      <span className="fw-semibold fs-6 text-gray-400">
                                        Identity Trust Level
                                      </span>
                                      <span className="fw-bold fs-6">
                                        {listcustomer.proPercentage}%
                                      </span>
                                    </div>
                                    <div className="h-5px mx-3 w-100 bg-light mb-3">
                                      <div
                                        className="bg-success rounded h-5px "
                                        style={{
                                          width: `${listcustomer.proPercentage}%`,
                                        }}
                                        role="progressbar"
                                        aria-valuenow={
                                          listcustomer.proPercentage
                                        }
                                        aria-valuemin="0"
                                        aria-valuemax="100"
                                      ></div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="card card-flush">
                        <div className="card-header pt-5">
                          <h3 className="card-title align-items-start flex-column">
                            <span className="card-label fw-bold text-dark fs-3">
                              KYC
                            </span>
                            <span className="text-gray-400 mt-1 fw-semibold fs-6">
                              User Kyc
                            </span>
                          </h3>
                        </div>

                        <div className="card-body">
                          <div className="d-flex flex-stack animation-css">
                            <div className="d-flex align-items-center jus me-3">
                              <img
                                src="../assets_new/images/digitalid-03.png"
                                className="me-3 w-30px"
                                alt=""
                              />

                              <div className="flex-grow-1">
                                <a
                                  href="#"
                                  className="text-gray-800 text-hover-primary fs-5 fw-bold lh-0"
                                >
                                  Fingerprint
                                </a>
                              </div>
                            </div>

                            <div className="d-flex justify-content-end align-items-center w-100 mw-125px">
                              {listcustomer.fingerPrint == 1 ? (
                                <img
                                  src="../assets_new/images/digital-04.png"
                                  width="25px"
                                />
                              ) : (
                                <img
                                  src="../assets_new/images/cross.png"
                                  width="23px"
                                />
                              )}
                            </div>
                          </div>

                          <div className="separator separator-dashed my-4"></div>

                          <div className="d-flex flex-stack animation-css">
                            <div className="d-flex align-items-center me-3">
                              <img
                                src="../assets_new/images/icons-16.png"
                                className="me-3 w-30px"
                                alt=""
                              />

                              <div className="flex-grow-1">
                                <a
                                  href="#"
                                  className="text-gray-800 text-hover-primary fs-5 fw-bold lh-0"
                                >
                                  Facial Identification
                                </a>
                              </div>
                            </div>

                            <div className="d-flex justify-content-end align-items-center w-100 mw-125px">
                              {listcustomer.facialIdentification == 1 ? (
                                <img
                                  src="../assets_new/images/digital-04.png"
                                  width="25px"
                                />
                              ) : (
                                <img
                                  src="../assets_new/images/cross.png"
                                  width="23px"
                                />
                              )}
                            </div>
                          </div>

                          <div className="separator separator-dashed my-4"></div>

                          <div className="d-flex flex-stack animation-css">
                            <div className="d-flex  align-items-center me-3">
                              <img
                                src="../assets_new/images/address.png"
                                className="me-3 w-30px"
                                alt=""
                              />

                              <div className="flex-grow-1">
                                <a
                                  href="#"
                                  className="text-gray-800 text-hover-primary fs-5 fw-bold lh-0"
                                >
                                  Location
                                </a>
                              </div>
                            </div>

                            <div className="d-flex justify-content-end align-items-center w-100 mw-125px">
                              {listcustomer.Location == 1 ? (
                                <img
                                  src="../assets_new/images/digital-04.png"
                                  width="25px"
                                />
                              ) : (
                                <img
                                  src="../assets_new/images/cross.png"
                                  width="23px"
                                />
                              )}
                            </div>
                          </div>

                          <div className="separator separator-dashed my-4 mbstyle"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="card rounded-15 mb-5 mb-xl-10 bgi-no-repeat bgi-position-x-end bgi-size-cover">
                  <div className="card-body  pt-9 pb-0 ">
                    <div className="container-xxl " id="kt_content_container">
                      <div className="row my-5">
                        <label className="col-lg-4 col-form-label  fw-semibold fs-6">
                          {" "}
                          <i
                            class="fas fa-sack-dollar"
                            style={{ fontSize: "2rem" }}
                          ></i>{" "}
                          <span
                            style={{ fontSize: "2rem", marginLeft: "1rem" }}
                          >
                            {" "}
                            Loan Apply{" "}
                          </span>{" "}
                        </label>

                        <div className="col-lg-8 fv-row my-0">
                          {alertmessagehandler ? (
                            <div class="alert alert-danger mt-5 mb-0">
                              Select your Organisation
                            </div>
                          ) : (
                            ""
                          )}
                        </div>
                      </div>

                      <form onSubmit={loanformhandler}>
                        <div className="row my-5">
                          <label className="col-lg-4 col-form-label  fw-semibold fs-6">
                            Organisation
                          </label>

                          <div className="col-lg-8 fv-row .bg-light">
                            <select
                              className="form-select border-0 flex-grow-1"
                              name="orgID"
                              onChange={handleChange}
                            >
                              <option value="1" selected="selected">
                                {" "}
                                Select Organisation
                              </option>
                              {listorg.map((item) => (
                                <option key={item._id} value={item._id}>
                                  {item.name}
                                </option>
                              ))}
                            </select>
                          </div>
                        </div>
                        <div className="row my-5">
                          <label className="col-lg-4 col-form-label  fw-semibold fs-6">
                            Type of Loan
                          </label>

                          <div className="col-lg-8 fv-row">
                            <select
                              className="form-select border-0 flex-grow-1"
                              onDoubleClick={() => alertmessage()}
                              name="Loan_type"
                              onChange={handleinterest}
                            >
                              <option value="1" selected="selected">
                                {" "}
                                Select loan Type
                              </option>
                              {interest.map((item, index) => (
                                <option key={index} value={interest[index]}>
                                  {item}
                                </option>
                              ))}
                            </select>
                          </div>
                        </div>
                        <div className="row my-5">
                          <label className="col-lg-4 col-form-label required fw-semibold fs-6">
                            Intrest Rate
                          </label>

                          <div className="col-lg-8 fv-row">
                            <input
                              type="text"
                              name="Interest"
                              className="form-control form-control-lg form-control-solid mb-3 mb-lg-0"
                              placeholder=" type Intrest Rate"
                              readOnly
                              defaultValue={interestbranch[1]}
                            />
                          </div>
                        </div>

                        <div className="row my-5">
                          <label className="col-lg-4 col-form-label required fw-semibold fs-6">
                            Amount
                          </label>

                          <div className="col-lg-8 fv-row">
                            <input
                              type="text"
                              name="Amount"
                              className="form-control form-control-lg form-control-solid mb-3 mb-lg-0"
                              placeholder=" enter Amound"
                            />
                          </div>
                        </div>
                        <div className="row my-5">
                          <label className="col-lg-4 col-form-label required fw-semibold fs-6">
                            Duration
                          </label>

                          <div className="col-lg-8 fv-row">
                            <input
                              type="text"
                              name="Emi_Months"
                              className="form-control form-control-lg form-control-solid mb-3 mb-lg-0"
                              placeholder=" enter Duration"
                            />
                          </div>
                        </div>

                        <div className="row my-5 d-none ">
                          <label className="col-lg-4 col-form-label  required fw-semibold d-none fs-6">
                            custID
                          </label>

                          <div className="col-lg-8 fv-row">
                            <input
                              type="text"
                              name="otp"
                              className="form-control  form-control-lg d-none form-control-solid mb-3 mb-lg-0"
                              placeholder=" enter otp"
                              value={otp}
                              defaultValue={otp}
                            />
                          </div>
                        </div>
                        <form>
                          <div className="row my-5">
                            <label className="col-lg-4 col-form-label required fw-semibold fs-6">
                              OTP{" "}
                            </label>

                            <div className="col-lg-4 fv-row">
                              <input
                                type="text"
                                name="otp"
                                className="form-control form-control-lg form-control-solid mb-3 mb-lg-0"
                                placeholder=" enter otp"
                                onChange={(e) => setotp(e.target.value)}
                              />
                            </div>
                            <div className="col-lg-4 fv-row">
                              <button
                                type="button"
                                className="btn btn-light btn-active-light-primary me-2"
                                onClick={(e) => AddFormotp(e)}
                              >
                                Get otp
                              </button>
                            </div>
                          </div>
                        </form>

                        <div className="card-footer d-flex justify-content-end py-6 px-9">
                          <button
                            type="submit"
                            className="btn btn-light btn-active-light-primary me-2"
                            onClick={() => handleShow()}
                          >
                            Submit
                          </button>
                        </div>
                      </form>
                      <div></div>
                    </div>
                  </div>
                </div>

                {/* added document in user */}

                <div className="row">
                  <div className="col-lg-12 mb-4">
                    <h2 className="fw-bold mt-5">
                      User Documents
                      {listcustomer == null ? (
                        <img
                          src="/assets_new/images/verify.png"
                          width="28px"
                          style={{ marginLeft: "1rem" }}
                        />
                      ) : (
                        ""
                      )}{" "}
                    </h2>
                  </div>

                  <div className="row mt-4  mb-2">
                    <div className="col-lg-4 basicdetail mb-2">
                      <div className="carduser">
                        <div className="text-center">
                          <a className="img_div">
                            <img src={listcustomer.residance} />
                          </a>
                          <a href="#" className="viewimg">
                            <i
                              className="fa fa-eye"
                              data-toggle="lightbox"
                              data-gallery="gallery"
                              aria-hidden="true"
                            ></i>
                          </a>
                        </div>
                        <div className="d-flex justify-content-between p-3">
                          <h5>Proof of Residence</h5>
                          <div>
                            <a href={listcustomer.residance} className="ml-2">
                              <i
                                className="fa fa-download"
                                aria-hidden="true"
                              ></i>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="col-lg-4 basicdetail mb-2">
                      <div className="carduser">
                        <div className="text-center">
                          <a className="img_div">
                            <img src={listcustomer.locaDocument} />
                          </a>

                          <a
                            className="viewimg"
                            href="#"
                            data-toggle="lightbox"
                            data-gallery="gallery"
                          >
                            <i
                              className="fa fa-eye"
                              aria-hidden="true"
                              data-toggle="lightbox"
                              data-gallery="gallery"
                            ></i>
                          </a>
                        </div>
                        <div className="d-flex justify-content-between p-3">
                          <h5>Local Document</h5>
                          <div>
                            <a
                              href={listcustomer.locaDocument}
                              className="ml-2"
                            >
                              <i
                                className="fa fa-download"
                                aria-hidden="true"
                              ></i>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="col-lg-4 basicdetail mb-2">
                      <div className="carduser">
                        <div className="text-center">
                          <a className="img_div" href="#">
                            <img src={listcustomer.landRegistration} />
                          </a>
                          <a
                            className="viewimg"
                            href="#"
                            data-toggle="lightbox"
                            data-gallery="gallery"
                          >
                            <i className="fa fa-eye" aria-hidden="true"></i>
                          </a>
                        </div>
                        <div className="d-flex justify-content-between p-3">
                          <h5>Land Registration</h5>
                          <div>
                            <a
                              href={listcustomer.landRegistration}
                              className="ml-2"
                            >
                              <i
                                className="fa fa-download"
                                aria-hidden="true"
                              ></i>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
