    const limit = 5;

  


   
  

   
    const userlist = () =>{
        const {orgID} = {orgID: _id}
         axios.get(`/v1/admin/orgDashSection/${orgID}`)
        .then(resp =>{
            let data =resp.data.data;
            setData(data);
   
        }).catch(error)

    }
    const userdoc = () =>{
        const {orgID} = {orgID: _id}
         axios.post(`/v1/admin/viewDoc/${orgID}`)
        .then(resp =>{
            let data =resp.data.findDoc;
            setdocument(data);
            console.log( "data in org documeny 4444444",data)
        }).catch(error)

    }
 

  const findOrguser = (e)=>{
    e.preventDefault();
    const data = new FormData(e.target);
    const Formvlaues = Object.fromEntries(data.entries());
      const { custID } = { custID: _id };
     axios.post(`/v1/admin/OrgCust/${custID}`,Formvlaues).then((resp) => {
        const data = resp.data.findCust;
        setOrguselistdata(data)
     console.log("Org list usedata  filler data ",data)
      });
    


  
  }



// first Pagination start

const Orguselist = async () => {
  const { custID } = { custID: _id };
  await axios.post(`/v1/admin/OrgCust/${custID}`).then((resp) => {
    const data = resp.data.findCust;
    setOrguselistdata(data)
    const total = resp.data.totlaRow;
    const totalPage = Math.ceil(total / limit);
    setpageCount(totalPage);
 console.log("Org list usedata ",data)
  });
};

const fetchComments = async (page) => {
  const { custID } = { custID: _id };
  const senData = { page: page };
  axios.post(`/v1/admin/OrgCust/${custID}`,senData).then((resp) => {
    const data = resp.data.findCust;
    setOrguselistdata(data)
 console.log("Org list usedata ",data)
  });
  return data;
};
const handlePageClick = async (data) => {

  const page = data.selected + 1;
  const commentsFormServer = await fetchComments(page);
  setData(commentsFormServer);
};


// end first Pagination

